
import Navbar from "@/components/Navbar.vue";
import PreLoader from "@/components/PreLoader.vue";
import { Options, Vue } from "vue-class-component";
@Options({
  name: "App",
  components: {
    Navbar,
    PreLoader,
  },
})
export default class App extends Vue {
  showPreLoad = true;

  togglePreLoad() {
    setTimeout(() => {
      this.showPreLoad = false;
    }, 1500);
  }

  mounted() {
    this.togglePreLoad();
  }
}
